3. CHỦ ĐỀ TÍM:
   --primary-color: #9b59b6;
   --primary-hover: #8e44ad;
   --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);

4. CHỦ ĐỀ CAM:
   --primary-color: #e67e22;
