6. CHỦ ĐỀ TỐI (Dark Mode):
   Sử dụng nút Dark Mode toggle trong header để chuyển đổi.
   Dark mode sẽ tự động thay đổi tất cả màu sắc phù hợp.

📝 LƯU Ý:
- Sau khi thay đổi, save file và refresh browser
- Có thể thay đổi từng màu riêng lẻ hoặc cả bộ theme  
